import React, { Component } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { setCurrentVideo } from '.././actions/current_video';

const VideosContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 250px;  
`
const Video = styled.div`
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-bottom: 10px;
  padding: 5px;
  width: 250px;
  border: 3px solid palevioletred;
  cursor: pointer;
`
const VideosTitle = styled.li`
  box-sizing: border-box;
  word-wrap: break-word;  
  list-style-type: none;
  padding-left: 10px;
  padding-right: 5px;
  color: palevioletred;
  max-width: 130px;
`
const VideosImg = styled.img`
  max-width: 120px;
  max-height: 90px;
`

class VideoList extends Component {
  constructor(props) {
    super(props)
  }
  render(){
    return (
      <VideosContainer>
        {this.props.store.videos.map((vidos, index) => 
          <Video key={vidos.etag} onClick={ event => this.props.onChangeCurrentVideo(vidos)}>
            <VideosImg key={`image ${index}`} src={vidos.snippet.thumbnails.default.url}></VideosImg>
            <VideosTitle key={index}>{vidos.snippet.title}</VideosTitle>
          </Video>
          )}
      </VideosContainer>
    )
  }
  }

  export default connect(
    state => ({
      store: state,
    }),
    dispatch => ({
      onChangeCurrentVideo: (video) => {
        dispatch(setCurrentVideo(video));
      }
    })
  )(VideoList);


